export function isToolEnabled(toolName, options) {
  if (options.tools && options.tools.blacklist && options.tools.blacklist.indexOf(toolName) !== -1) {
    return false;
  }

  if (options.tools && options.tools.whitelist && options.tools.whitelist.indexOf(toolName) === -1) {
    return false;
  }

  return true;
}